import { OnInit, Component, Input, ViewChild, Injector, EventEmitter,
    Output, ChangeDetectorRef, OnChanges, SimpleChanges } from '@angular/core';
import { MultiSelectComponent } from '@farris/ui-multi-select';
@Component({
    selector: 'view-columns',
    templateUrl: './datagrid-view-columns.component.html',
})
export class DatagridViewColumnsComponent implements OnInit, OnChanges {
    @Input() idField = 'field';
    @Input() valueField = 'field';
    @Input() textField = 'title';


    private _selectedIds = [];
    get selectedIds() {
        return this._selectedIds;
    }
    @Input() set selectedIds(vals) {
        this._selectedIds = vals;
        this.selectedIdsChange.emit(vals);
    }

    @Input() treeData = [];
    @ViewChild('ms') ms: MultiSelectComponent;

    @Output() selectedIdsChange = new EventEmitter();

    constructor(private injector: Injector, private cd: ChangeDetectorRef) {}


    ngOnInit() {}

    ngOnChanges(changes: SimpleChanges) {

    }

    onBeforeMove($event) {

    }

    hiddenColumn($event: MouseEvent, column: any) {
        $event.stopPropagation();
        this.ms.moveToLeft(column);
    }
}
