import { OnInit, Component, Input, ViewChild, Injector, EventEmitter, Output, ChangeDetectorRef } from '@angular/core';
import { MultiSelectComponent } from '@farris/ui-multi-select';

@Component({
    selector: 'sort-setting',
    templateUrl: './datagrid-sort-setting.component.html'
})
export class DatagridSortSettingComponent implements OnInit {

    @Input() idField = 'field';
    @Input() valueField = 'field';
    @Input() textField = 'title';
    @Input() selectedIds = [];

    @Input() treeData = [];
    @ViewChild('ms') ms: MultiSelectComponent;

    @Output() sortInfoChange = new EventEmitter();

    constructor(private injector: Injector, private cd: ChangeDetectorRef) {}


    ngOnInit() {}

    setFieldSortOrder($event: MouseEvent, data: any, order: string) {
        $event.stopPropagation();
        data.sortable = true;
        data.order = order;

        if (!order) {
            this.ms.moveToLeft(data);
        }

        this.cd.detectChanges();
        this.onSelectionsChange(null);
        return false;
    }

    getSortInfo() {
        if (this.ms.selections) {
            const names = [];
            const orders = [];
            this.ms.selections.forEach(col => {
                col.order = col.order || 'asc';
                names.push(col.field);
                orders.push(col.order);
            });

            return { sortName: names, sortOrder: orders };
        }
        return '';
    }

    onSelectionsChange($event) {
        const si = this.getSortInfo();
        this.sortInfoChange.emit(si);
    }

    onBeforeMove($event) {
        const items: any = $event.items as any;
        if ($event.target.indexOf('rightAll') > -1) {
            for (const id in items) {
                if ($event.items[id]) {
                    $event.items[id].order = 'asc';
                }
            }
        } else {
            if ($event.target === 'right') {
                for (const id in items) {
                    if ($event.items[id]) {
                        $event.items[id].order = 'asc';
                    }
                }
            }
        }
    }
}
